@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500&display=swap');
* {
  font-family: 'Maven Pro', sans-serif;
  text-align: center;
};

.button{
  text-decoration: none;
  padding: 10px;
  font-weight: 600;
  font-size: 20px;
  color: #ffffff;
  background-color: #6a6375ed;
  border-radius: 6px;
  border: 2px solid #6a6375fd;
  margin: 20px;
  cursor: pointer;
}
